export type City = {
	name: string
	country: string
	location: {
		latitude: number
		longitude: number
	}
	cameraPosition: [number, number, number]
	type: CityType
}

export type Post = {
	id: number
	title: string
	country: string
	city: string
	date: string
	type: CityType
}

export enum CityType {
	NewYork = 'New York',
	MexicoCity = 'Mexico City',
	Guanajuato = 'Guanajuato',
	Puebla = 'Puebla',
	Cuzco = 'Cuzco',
	AguasCalientes = 'Aguas Calientes',
	LaPaz = 'La Paz',
	Uyuni = 'Uyuni',
	Atacama = 'Atacama',
	PuertoNatales = 'Puerto Natales',
	ElCalafate = 'El Calafate',
	ElChalten = 'El Chalten',
	BuenosAires = 'Buenos Aires',
	Montevideo = 'Montevideo',
	Iguazu = 'Iguazu',
	RioDeJaneiro = 'Rio De Janeiro',
	SanPaulo = 'San Paulo',
	Frankfurt = 'Frankfurt',
}
