import React, { useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import '../assets/style.scss'
import Earth from '../components/Earth.tsx'
import useThrottleDebounce from '../hooks/useThrottleDebounce.ts'
import { contents } from '../assets/content.value.ts'
import Post from './Post.tsx'
import { cities } from '../assets/city.value.ts'
import { Post as PostType } from '../assets/types.ts'

function Home() {
	const [cameraPosition, setCameraPosition] = useState<[number, number, number]>([-3, -2, 9])
	const [active, setActive] = useState(0)
	const [activeContent, setActiveContent] = useState<any | null>(null)
	const navigate = useNavigate()
	const params = useParams()

	function goToPost(id: number) {
		if (id > 0) {
			return
		}

		navigate(`/${id}`)
	}

	function scrollEvent(e: any) {
		const target = e.target as HTMLTextAreaElement
		let position = Math.round(target.scrollTop / 126)
		let targetContent: null | PostType = null

		if (position > 40) {
			position = 40
		}

		setActive(position)
		if (position > 0) {
			targetContent = contents[position - 1]
		}

		if (targetContent) {
			setActiveContent(targetContent)
			const targetCity = cities.find((el) => el.type === targetContent.type)
			setCameraPosition(targetCity?.cameraPosition ?? cameraPosition)
		} else {
			setActiveContent(targetContent)
		}
	}

	const updateScroll = useThrottleDebounce(scrollEvent, 200)

	return (
		<div className="container">
			<div>
				{activeContent ? <p className="city-name playwrite">{activeContent.type}</p> : undefined}
				<Post />
				<Earth active={activeContent} cameraPosition={cameraPosition}></Earth>
			</div>
			<div>
				{/* <div className="lang">KR / ES</div> */}
				<div className="list-container">
					<div className="padding"></div>
					<div className="title">
						<p className="sacramento-regular fs-60">Viaje de Mia</p>
						<p className="kr-title">미아의 여행</p>
					</div>
					<div className="inner-container" onScroll={updateScroll}>
						{contents.map((item, index) => {
							return (
								<div key={index} onClick={() => goToPost(item.id)} className="item-container">
									<button
										key={item.id}
										className={
											active === index + 1 || parseInt(params?.id ?? '0') === index + 1 ? 'active' : ''
										}
									>
										<span>{item.id}</span>
									</button>
									<span className="fs-14 item-title">{item.title}</span>
									<span className="playwrite-300 fs-12">{item.date}</span>
								</div>
							)
						})}
					</div>
				</div>
			</div>
		</div>
	)
}

export default Home
