import { useCallback, useRef } from 'react'

const useThrottleDebounce = (callback, throttleTime, debounceTime = 200) => {
	const throttleTimeout = useRef<NodeJS.Timeout | null>(null)
	const debounceTimeout = useRef<NodeJS.Timeout | null>(null)
	const lastExecuted = useRef(0)

	const throttledDebouncedCallback = useCallback(
		(...args) => {
			const now = Date.now()

			// Throttle
			if (now - lastExecuted.current >= throttleTime) {
				lastExecuted.current = now
				callback(...args)
			} else if (!throttleTimeout.current) {
				throttleTimeout.current = setTimeout(() => {
					lastExecuted.current = Date.now()
					throttleTimeout.current = null
					callback(...args)
				}, throttleTime - (now - lastExecuted.current))
			}

			// Debounce
			if (debounceTimeout.current) {
				clearTimeout(debounceTimeout.current)
			}
			debounceTimeout.current = setTimeout(() => {
				callback(...args)
			}, debounceTime)
		},
		[callback, throttleTime, debounceTime]
	)

	return throttledDebouncedCallback
}

export default useThrottleDebounce
