import React, { useEffect } from 'react'
import { Canvas, useThree } from '@react-three/fiber'
import { OrbitControls } from '@react-three/drei'
import * as THREE from 'three'
import { drawThreeGeo } from '../assets/threeGeoJSON.js' // Ensure this is compatible with your setup
import { cities } from '../assets/city.value.ts'
import { Post } from '../assets/types.ts'

const Sphere = () => {
	// Add wireframe sphere
	return (
		<lineSegments>
			<edgesGeometry args={[new THREE.SphereGeometry(2)]} />
			<lineBasicMaterial color={0x000000} transparent opacity={0.4} />
		</lineSegments>
	)
}

const Marker = ({
	latitude,
	longitude,
	selected,
}: {
	latitude: number
	longitude: number
	selected?: boolean
}) => {
	// Calculate position from latitude and longitude
	const radius = 2.04
	const phi = (90 - latitude) * (Math.PI / 180)
	const theta = (longitude + 180) * (Math.PI / 180)

	const x = radius * Math.sin(phi) * Math.cos(theta)
	const y = radius * Math.cos(phi)
	const z = radius * Math.sin(phi) * Math.sin(theta)

	return (
		<mesh position={[x, y, z]}>
			<sphereGeometry args={selected ? [0.04, 8, 8] : [0.025, 8, 8]} />
			<meshBasicMaterial color={selected ? 0xff0000 : 0x000000} />
		</mesh>
	)
}

const Countries = () => {
	const { scene } = useThree()

	useEffect(() => {
		fetch('/content/countries.json')
			.then((response) => response.text())
			.then((text) => {
				const data = JSON.parse(text)
				const countries = drawThreeGeo({
					json: data,
					radius: 2,
					materialOptions: {
						color: 0x80ff80,
					},
				})
				// const axis = new THREE.Vector3(1, 1.5, 0) // X-axis
				// const angle = Math.PI / 4 // 45 degrees
				// countries.rotateOnAxis(axis, angle)

				// countries.rotation.x = Math.PI / 40
				scene.add(countries)
			})
	}, [scene])

	return null // This component only adds to the scene
}

const Scene = ({
	cameraPosition,
	active,
}: {
	active: Post
	cameraPosition: [number, number, number]
}) => {
	const { camera } = useThree()

	useEffect(() => {
		const newZ = -(cameraPosition[0] * Math.cos(5.49779)) - cameraPosition[2] * Math.sin(5.49779)
		const newX = cameraPosition[0] * Math.cos(0.78) + cameraPosition[2] * Math.sin(0.78)

		camera.position.set(newX, cameraPosition[1], newZ)
	}, [cameraPosition])

	return (
		<>
			<fogExp2 attach="fog" color="white" density={0.3} />
			<Sphere />
			{cities.map((city) => (
				<Marker
					key={city.name}
					latitude={city.location.latitude}
					longitude={city.location.longitude}
					selected={city.type === active?.type}
				/>
			))}

			<Countries />
			<ambientLight intensity={0.2} />
		</>
	)
}

