import React, { useEffect } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import Markdown from 'react-markdown'
import '../assets/style.scss'

export function Post() {
	const [readable, setReadable] = React.useState({ md: '' })
	const navigate = useNavigate()
	const { id } = useParams()

	const getFileName = (id: string | number): string => {
		const num = parseInt(id as string)

		if (num < 10) {
			return `0${num}`
		}
		return num.toString()
	}

	useEffect(() => {
		if (!id) {
			return
		}

		fetch(`./content/${getFileName(id)}.md`)
			.then((res) => res.text())
			.then((md) => {
				setReadable({ md })
			})
	}, [id])

	if (!id) {
		return
	}

	if (readable.md.includes('DOCTYPE')) {
		return (
			<div className="container">
				<div className="lang">KR / ES</div>
				<div className="post-container">
					404 NOT FOUND
					<button onClick={() => navigate('/')} className="ico-close" type="button">
						x
					</button>
				</div>
			</div>
		)
	}

	return (
		<div className="post-container">
			<Markdown>{readable.md}</Markdown>
			<button onClick={() => navigate('/')} className="ico-close" type="button">
				x
			</button>
		</div>
	)
}

export default Post
